"use client";

import { useSpaceData } from "@/src/context/SpaceDataContext";
import { Destination } from "@/src/types";
import React, { useEffect, useState } from "react";

export default function page() {
  const { data } = useSpaceData();
  const [destination, setDestination] = useState<Destination>();

  useEffect(() => {
    if (destination || !data) return;
    setDestination(
      data?.destinations.find(
        (destination: Destination) => destination.name === "Moon"
      )
    );
  }, [data]);

  if (!data) return null;

  return (
    <main className="h-full flex flex-col gap-8 items-center ">
      <p className="text-3xl text-silver font-semibold barlowCondensed">
        <span className="text-zinc-600">01</span> PICK YOUR DESTINATION
      </p>

      {/* IMAGE HOLDER */}
      <section className="w-full flex items-center justify-center">
        <div className="w-full aspect-square bg-blue-300 rounded-full max-w-[300px]">
          <Image src={destination?.images.webp} alt={destination.name}></Image>
        </div>
      </section>
    </main>
  );
}
